<template>
  <div class="container d-flex px-0 align-items-center" style="minHeight:80vh;height:80vh">
    <div class="login container px-0">
      <div class="row container pt-5 px-0">
        <div class="col s12 px-0">
          <div class="card-panel mt-5 d-flex flex-column justify-content-center px-0">
            <h5 class="text-center">Sign In</h5>
            <br />
            <div
              class="btn-container"
              style="display:grid;justifyContent:center"
            >
              <button
                class="waves-effect waves-light btn white teal-text d-flex align-items-center py-4 px-5"
                v-on:click.prevent="login"
                :disabled="loggedIn"
              >
                <img
                  class="responsive-img rounded-circle mr-3"
                  src="../assets/g_logo.png"
                  width="20"
                />
                <span class="text-capitalize" style="color:#263238;fontWeight:500"
                  >sign in with google</span
                >
              </button>
            </div>

            <div class="row d-flex align-items-center px-5 pt-5">
              <div class="col s5"><div class="divider"></div></div>
              <div class="col s2 px-0 text-center">or</div>
              <div class="col s5"><div class="divider"></div></div>
            </div>

            <div class="row mb-0 d-flex justify-content-center px-4">
              <div class="col s12 m8 ml-0 input-field">
                <input
                  id="user"
                  type="text"
                  class="validate"
                  autocomplete="false"
                />
                <label for="user">User/Email</label>
              </div>
            </div>
            <div class="row d-flex justify-content-center mb-0 px-4">
              <div class="col s12 m8 ml-0 input-field">
                <input id="password" type="password" class="validate" />
                <label for="password">Password</label>
              </div>
            </div>
            <div class="row d-flex justify-content-center p-3">
              <div class="col s12 m8 ml-0 input-field py-0">
                <button class="waves-effect waves-light btn light-blue darken-2">Sign In</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters, mapMutations } from "vuex";
export default {
  name: "Login",
  data() {
    return {
      isLogin: true,
    };
  },
  computed: {
    ...mapGetters("user", {
      user: "user",
      loggedIn: "loggedIn",
    }),
  },
  methods: {
    ...mapMutations(["toggleLoader"]),
    ...mapActions("user", {
      login: "login",
      logout: "logout",
    }),
  },
  mounted() {
    this.toggleLoader();
  },
};
</script>

